import React from "react";

const ShopWork = () => {
    return (
        <div className="content-wrapper">
            <div className="shop-work-wrapper">
                <div className="poster">
                    <img src="static/img/stack/socket.png" className="socket-icon" alt="" />
                    <img src="static/img/stack/node.png" className="node-icon" alt="" />
                    <div className="card">
                        <img className="card-img" src="static/img/works/shop.png" alt="" />
                        <div className="card-description">
                            <a href="http://shoesbank.gherciu.gq" target="_blank" className="card-btn">
                                <img src="static/img/icons/see.svg" />
                                View demo
                            </a>
                        </div>
                    </div>
                </div>
                <div className="content">
                    <h2 className="content-text-heading">ShoesBank.</h2>
                    <h3 className="content-text-subheading">A online shoe store for different types of sports.</h3>
                    {/* <a target='_blank' href='' className='button-link -view'>View demo</a> */}
                </div>
            </div>
            <style jsx>{`
                .socket-icon {
                    max-width: 250px;
                    height: auto;
                    position: absolute;
                    bottom: 40px;
                    left: -105px;
                    transform: rotate(-90deg);
                }
                .node-icon {
                    max-width: 350px;
                    height: auto;
                    position: absolute;
                    bottom: -180px;
                    right: -50px;
                    transform: rotate(10deg);
                }
                .shop-work-wrapper {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 0px;
                    align-items: center;
                }
                h3 {
                    margin-top: 10px;
                }
                .content {
                    max-width: 390px;
                }
                .content-text-heading {
                    text-align: right;
                }
                .content-text-subheading {
                    text-align: right;
                }
                .poster {
                    max-width: 560px;
                    width: 100%;
                    padding: 20px 0px;
                    display: flex;
                    justify-content: center;
                    position: relative;
                }
                .card {
                    max-width: 480px;
                    z-index: 2;
                }
                @media (max-width: 860px) {
                    .shop-work-wrapper {
                        flex-direction: column-reverse;
                    }
                    .content-text-heading {
                        text-align: center;
                    }
                    .content-text-subheading {
                        text-align: center;
                    }
                    .node-icon {
                        display: none;
                    }
                    .socket-icon {
                        display: none;
                    }
                }
            `}</style>
        </div>
    );
};

export default ShopWork;
